	module.exports = {"data":{"markdownRemark":{"html":"<p>I presented at my organizations quarterly Lectora brownbag luncheon this week on using Lectora and code (HTML/CSS/and JavaScript). It was a lot of fun preparing and I believe (hope!) that everyone found it useful. I wanted to post the presentation here as a demo of the types of things I have done and hope to do with code in Lectora.</p>\n<h2>Why Code?</h2>\n<p>There are two main reasons. The first one is that, when I first started using Lectora, I dug through the forums and one of the things I noticed pretty quickly was Lectora could be an extemely powerful and versatile tool. Thing was, the key to that power was the ability to insert code (particularly JavaScript) while developing—so, pre-production—to move beyond what you could do with Lectora natively. The problem was that, though I was familiar with HTML and CSS, I’d never worked with JavaScript (and unfortunately, the users in the Lectora forums didn’t seem to be super-forthcoming in showing people how to do these things). So I’ve set about this year on a serious mission to learn to code and, through that, I’ve found that I’ve expanded my definition of what I can do inside of Lectora. The second reason is that, when I have room to run (which I sometimes do) outside of the constraints of Lectora, knowing how to create cool interactions, interactive infographics, webpages, etc. is one way to create really cool learning objects that aren’t tied to any authoring tool. Being able to expand my thinking outside of “what can I do in Lectora?” is highly valuable when I’m storyboarding.</p>\n<h2>About the Presentation</h2>\n<p>I set out to think a little more broadly than dissecting a particular interaction since this is an inter-divisional luncheon and all of us have really specific target audiences. So I thought it would be useful to show some grab-and-go type code snippets that folks could use in their own projects. I also thought about the presentation in terms of simple to complex. In the first section, I covered things that I’ve actually done with code in courses I’ve created, moving from simple things (like changing the hover color of a link with CSS) to more complex interactions (like drawing on the screen). In the second part, I demoed even more complex interactions that can be created with code to illustrate the types of places I’d like to go with Lectora in the future. <strong>Note:</strong> On the penultimate slide, I show a magnification plugin and, if you look closely, you’ll see that it isn’t working quite right. I haven’t yet been able to figure out the necessary math to make this work with the image. But I will!</p>\n<h2>The Presentation!</h2>\n<p>Best viewed in the latest Firefox or Chrome browsers on a desktop or laptop. Click the image to view.</p>\n<figure>\n  <a href=\"http://knanthony.com/showcase/LectoraPreso/index.html\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 65.54455445544555%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Presentation slide\" title=\"\" src=\"/static/LectoraPreso-fce610cee7d00daf12a2d049112c02a0-1626e.png\" srcset=\"/static/LectoraPreso-fce610cee7d00daf12a2d049112c02a0-076ec.png 203w,\n/static/LectoraPreso-fce610cee7d00daf12a2d049112c02a0-7ae34.png 405w,\n/static/LectoraPreso-fce610cee7d00daf12a2d049112c02a0-1626e.png 810w,\n/static/LectoraPreso-fce610cee7d00daf12a2d049112c02a0-c8481.png 1010w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n  <figcaption>Click to view the presentation!</figcaption>\n</figure>\n<h2>To Be Continued…</h2>\n<p>My hope is to continue to create micro-courses on many of the things that I demoed so that other Lectora users can take the code snippets, learn how to use them, and tweak them to fit their own projects. You can find mini-courses I’ve already created here: <a href=\"http://knanthony.com/courses/\">http://knanthony.com/courses/</a> I’d love some feedback.</p>\n<h2>Credits</h2>\n<h3>Images</h3>\n<ul>\n<li><a href=\"http://commons.wikimedia.org/wiki/File:Phra_Nang_beach_panorama_edit.jpg\">Phra Nang beach panorama</a> | Wikimedia user kallerna</li>\n<li><a href=\"http://commons.wikimedia.org/wiki/File:Cursive.svg\">D’Nealian cursive manuscript</a> | Wikimedia user Andrew Buck</li>\n</ul>\n<h3>Code Projects</h3>\n<ul>\n<li><a href=\"http://codepen.io/mikethomas/pen/nHfqA\">Walking Cycle Using CSS Animations</a> | Mike Thomas</li>\n<li><a href=\"http://tympanus.net/codrops/2013/06/18/caption-hover-effects/\">Caption Hover Effects</a> | Mary Lou</li>\n<li><a href=\"http://zurb.com/playground/twentytwenty\">TwentyTwenty</a> (before and after slider) | Zurb Studios</li>\n<li><a href=\"http://codepen.io/edankwan/pen/emqgpr\">CSS Non-webgl realistic globe Demo</a> | Edan Kwan</li>\n<li><a href=\"http://www.vincentbroute.fr/mapael/\">Mapael Project</a> (interactive map) | Vincent Brouté</li>\n<li><a href=\"http://tutorialzine.com/2010/06/apple-like-retina-effect-jquery-css/\">Apple Retina Effect</a> (magnify search) | Martin Angelov</li>\n</ul>","frontmatter":{"date":"April 30, 2015","path":"/blog/12-things-you-can-do-with-lectora-and-code","title":"12 Things You Can Do with Lectora and Code","tags":["code","coding","css","html","JavaScript","Lectora Inspire"],"categories":["Coding","elearning","Showing my Work"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/Lectora-Code-a9ef9a8b4873ee6a59751a12ffdcace2-81d39.png","srcSet":"/static/Lectora-Code-a9ef9a8b4873ee6a59751a12ffdcace2-9a8d1.png 203w,\n/static/Lectora-Code-a9ef9a8b4873ee6a59751a12ffdcace2-c53f9.png 405w,\n/static/Lectora-Code-a9ef9a8b4873ee6a59751a12ffdcace2-81d39.png 810w,\n/static/Lectora-Code-a9ef9a8b4873ee6a59751a12ffdcace2-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

